:root {
  --margin-t: 0;
  --margin-r: 0;
  --margin-b: 0;
  --margin-l: 0;
  --padding-t: 0;
  --padding-r: 0;
  --padding-b: 0;
  --padding-l: 0;
  --d-inline: inline;
  --d-block: block;
  --d-inline-block: inline-block;
  --d-flex: flex;
  --d-inline-flex: inline-flex;
  --d-grid: grid;
  --primary-background: #f6f6f6;
  --primary-color: #000;
  --primary-font: Inter, sans-serif;
  --secondary-font: Inika, sans-serif;
  --adaptive-box: 100%;
  --container-max-width: 100vw;
  --order: 0;
  --text-color: inherit;
  --background-color: none;
  --grid-gap-x: 0.5rem;
  --grid-gap-y: 0.5rem;
}

* {
  box-sizing: border-box;
  -webkit-tap-highlight-color: transparent;
}

html {
  font-size: 4.2105263158vw;
}

@font-face {
  font-family: "Material Symbols Outlined";
  src: url("../fonts/MaterialSymbolsOutlined24pt-Regular.woff2") format("woff2"), url("../fonts/MaterialSymbolsOutlined24pt-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
}

html {
  scroll-behavior: smooth;
}

body {
  display: grid;
  grid-template-rows: auto 1fr auto;
  grid-template-areas: "header" "main" "footer";
  min-height: 100vh;
  margin: 0;
  font-family: var(--primary-font);
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.5625rem;
  background: #EDF2EC;
  color: #636060;
}

body.hidden {
  overflow: hidden;
}

[class*=grid-] {
  --d-grid: grid;
  display: var(--d-grid);
  grid-template-columns: repeat(var(--grid-column), 1fr);
  grid-column-gap: var(--grid-gap-x);
  grid-row-gap: var(--grid-gap-y);
  justify-items: var(--align-x);
  align-items: var(--align-y);
  position: relative;
}

[class*=grid-] > * {
  --grid-column-start: auto;
  --grid-column-end: auto;
  grid-column: var(--grid-column-start)/var(--grid-column-end);
  min-width: 1px;
}

.grid-center {
  --align-x: center;
  --align-y: center;
}

.grid-center-x {
  --align-x: center;
}

.grid-center-y {
  --align-y: center;
}

.grid-1 {
  --grid-column: 1;
}

.col-1 {
  --grid-column-end: span 1;
}

.cs-1 {
  --grid-column-start: 1;
}

.grid-2 {
  --grid-column: 2;
}

.col-2 {
  --grid-column-end: span 2;
}

.cs-2 {
  --grid-column-start: 2;
}

.grid-3 {
  --grid-column: 3;
}

.col-3 {
  --grid-column-end: span 3;
}

.cs-3 {
  --grid-column-start: 3;
}

.grid-4 {
  --grid-column: 4;
}

.col-4 {
  --grid-column-end: span 4;
}

.cs-4 {
  --grid-column-start: 4;
}

.grid-5 {
  --grid-column: 5;
}

.col-5 {
  --grid-column-end: span 5;
}

.cs-5 {
  --grid-column-start: 5;
}

.grid-6 {
  --grid-column: 6;
}

.col-6 {
  --grid-column-end: span 6;
}

.cs-6 {
  --grid-column-start: 6;
}

.grid-7 {
  --grid-column: 7;
}

.col-7 {
  --grid-column-end: span 7;
}

.cs-7 {
  --grid-column-start: 7;
}

.grid-8 {
  --grid-column: 8;
}

.col-8 {
  --grid-column-end: span 8;
}

.cs-8 {
  --grid-column-start: 8;
}

.grid-9 {
  --grid-column: 9;
}

.col-9 {
  --grid-column-end: span 9;
}

.cs-9 {
  --grid-column-start: 9;
}

.grid-10 {
  --grid-column: 10;
}

.col-10 {
  --grid-column-end: span 10;
}

.cs-10 {
  --grid-column-start: 10;
}

.grid-11 {
  --grid-column: 11;
}

.col-11 {
  --grid-column-end: span 11;
}

.cs-11 {
  --grid-column-start: 11;
}

.grid-12 {
  --grid-column: 12;
}

.col-12 {
  --grid-column-end: span 12;
}

.cs-12 {
  --grid-column-start: 12;
}

.container {
  --container-side-padding: 1.5625rem;
  width: 100%;
  max-width: var(--container-max-width);
  margin: 0 auto;
  padding-left: var(--container-side-padding);
  padding-right: var(--container-side-padding);
}

[class*=tal] {
  text-align: var(--t-align);
}

[class*=tac] {
  text-align: var(--t-align);
}

[class*=tar] {
  text-align: var(--t-align);
}

.tal {
  --t-align: left;
}

.tac {
  --t-align: center;
}

.tar {
  --t-align: right;
}

.left-border,
.left-border_primary,
.left-border_purple,
.left-border_red,
.left-border_sea,
.left-border_lime,
.left-border_carrot {
  border-left: 0.5rem solid;
}

.left-border_carrot {
  border-color: #ed8e00;
}

.left-border_lime {
  border-color: #8ebd00;
}

.left-border_sea {
  border-color: #29a4a2;
}

.left-border_red {
  border-color: #a90602;
}

.left-border_purple {
  border-color: #834fef;
}

.left-border_primary {
  border-color: #636060;
}

[class*=bg-color] {
  background-color: var(--background-color);
}

.bg-color_carrot {
  --background-color: #ed8e00;
}

.bg-color_lime {
  --background-color: #8ebd00;
}

.bg-color_sea {
  --background-color: #29a4a2;
}

.bg-color_red {
  --background-color: #a90602;
}

.bg-color_purple {
  --background-color: #834fef;
}

.bg-color_primary {
  --background-color: #636060;
}

.bg-color_corporate {
  --background-color: #ae0b14;
}

.bg-color_secondary {
  --background-color: #6c6c6c;
}

.bg-color_white {
  --background-color: #fff;
}

.bg-color_grey {
  --background-color: #ebebeb;
}

[class*=t-color] {
  color: var(--text-color);
}

.text-color__title {
  --text-color: #717171;
}

.text-color__content {
  --text-color: #636060;
}

.text-color__green {
  --text-color: #499A18;
}

.text-color__orange {
  --text-color: #E06733;
}

.text-color__white {
  --text-color: #fff;
}

.text-color__black {
  --text-color: #000;
}

.title,
.subtitle {
  --margin-t: 0;
  --margin-r: 0;
  --margin-b: 0;
  --margin-l: 0;
  --padding-t: 0;
  --padding-r: 0;
  --padding-b: 0;
  --padding-l: 0;
  --d-block: block;
  display: var(--d-block);
  margin: var(--margin-t) var(--margin-r) var(--margin-b) var(--margin-l);
  padding: var(--padding-t) var(--padding-r) var(--padding-b) var(--padding-l);
}

.title {
  font-family: var(--primary-font);
  font-size: 1.5625rem;
  font-weight: 700;
  line-height: 2.1875rem;
}

.subtitle {
  font-size: 1.5625rem;
  font-family: var(--secondary-font);
  font-weight: 400;
  line-height: 2rem;
}

.text {
  --margin-t: 0;
  --margin-r: 0;
  --margin-b: 0;
  --margin-l: 0;
  --padding-t: 0;
  --padding-r: 0;
  --padding-b: 0;
  --padding-l: 0;
  --d-block: block;
  display: var(--d-block);
  margin: var(--margin-t) var(--margin-r) var(--margin-b) var(--margin-l);
  padding: var(--padding-t) var(--padding-r) var(--padding-b) var(--padding-l);
}

.text--size-sm {
  font-size: 0.75rem;
  line-height: 1;
}

.text--size-md {
  font-size: 0.875rem;
  line-height: 1.14;
}

.text--size-lg {
  font-size: 0.875rem;
  line-height: 1.43;
}

ul.list {
  list-style: disc;
}

ol.list {
  list-style: none;
  counter-reset: item;
  padding-left: 0;
}

ol.list li:before {
  float: left;
  padding-right: 0.34em;
  text-align: right;
  box-sizing: border-box;
  content: counters(item, ".") ". ";
  counter-increment: item;
}

ol.list ol.list > li:before {
  width: 2.25rem;
}

.list {
  --margin-t: 0;
  --margin-r: 0;
  --margin-b: 0;
  --margin-l: 0;
  --padding-t: 0;
  --padding-r: 0;
  --padding-b: 0;
  --padding-l: 0;
  --d-block: block;
  display: var(--d-block);
  margin: var(--margin-t) var(--margin-r) var(--margin-b) var(--margin-l);
  padding: var(--padding-t) var(--padding-r) var(--padding-b) var(--padding-l);
  font-weight: normal;
}

.list--size-sm {
  font-size: 0.75rem;
  line-height: 1;
}

.list--size-md {
  padding-left: 1.3125rem;
  font-size: 0.875rem;
  line-height: 1.14;
}

.list--size-lg {
  font-size: 1.125rem;
  line-height: 1.1;
}

.list li {
  display: list-item;
}

.adaptive-box {
  --margin-t: 0;
  --margin-r: 0;
  --margin-b: 0;
  --margin-l: 0;
  --padding-t: 0;
  --padding-r: 0;
  --padding-b: 0;
  --padding-l: 0;
  --d-block: block;
  overflow: hidden;
  display: var(--d-block);
  width: 100%;
  position: relative;
  margin: var(--margin-t) var(--margin-r) var(--margin-b) var(--margin-l);
  padding: var(--padding-t) var(--padding-r) var(--padding-b) var(--padding-l);
}

.adaptive-box:before {
  content: "";
  display: block;
  padding: 0 0 var(--adaptive-box) 0;
}

.adaptive-box--image {
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
  object-position: center;
  object-fit: cover;
}

.btn {
  --margin-t: 0;
  --margin-r: 0;
  --margin-b: 0;
  --margin-l: 0;
  --padding-t: 0;
  --padding-r: 0;
  --padding-b: 0;
  --padding-l: 0;
  --d-inline-flex: inline-flex;
  display: var(--d-inline-flex);
  justify-content: center;
  align-items: center;
  margin: var(--margin-t) var(--margin-r) var(--margin-b) var(--margin-l);
  padding: var(--padding-t) var(--padding-r) var(--padding-b) var(--padding-l);
  color: #000;
  border: none;
  background-image: none;
  outline: none;
  box-shadow: none;
  font-family: var(--primary-font);
  font-size: 0.625rem;
  line-height: 1.25rem;
  white-space: nowrap;
  text-decoration: none;
  cursor: pointer;
  transition: 0.3s;
}

.btn:disabled,
.btn.disabled {
  background: #c5c5c5 !important;
  color: #fff !important;
  cursor: auto !important;
}

.btn-outline {
  padding: 0.5rem 1.375rem;
  background: transparent;
  color: #000;
  border: 0.125rem solid #E06733;
  border-radius: 0.625rem;
}

.btn-outline:hover {
  background: #E06733;
  color: #fff;
}

.btn-outline--orange {
  padding: 0.5rem 1.75rem;
  background: transparent;
  font-size: 0.9375rem;
  color: #E06733;
  border: 0.125rem solid #E06733;
  border-radius: 0.625rem;
}

.btn-outline--orange:hover {
  background: #E06733;
  color: #fff;
}

.icon,
.form--checkbox-icon,
.folder--arrow {
  --margin-t: 0;
  --margin-r: 0;
  --margin-b: 0;
  --margin-l: 0;
  --padding-t: 0;
  --padding-r: 0;
  --padding-b: 0;
  --padding-l: 0;
  --d-block: block;
  display: var(--d-block);
  --icon-size: 1.5rem;
  flex: 0 0 var(--icon-size);
  width: var(--icon-size);
  height: var(--icon-size);
  margin: var(--margin-t) var(--margin-r) var(--margin-b) var(--margin-l);
  padding: var(--padding-t) var(--padding-r) var(--padding-b) var(--padding-l);
  font-style: normal;
  object-position: center;
  object-fit: contain;
  color: var(--text-color);
  fill: currentColor;
}

.icon:after,
.form--checkbox-icon:after,
.folder--arrow:after {
  font-family: "Material Symbols Outlined";
  font-size: var(--icon-size);
  font-weight: 400;
  line-height: 1;
}

.breadcrumbs {
  --d-flex: flex;
  display: var(--d-flex);
  align-items: center;
  font-size: 0.875rem;
  line-height: 1.43;
  color: #a3a3a3;
}

.breadcrumbs:not(.horizontal-scroll) {
  flex-wrap: wrap;
}

.breadcrumbs a {
  text-decoration: none;
  color: inherit;
  transition: 0.3s;
}

.breadcrumbs a:hover {
  color: #636060;
}

.breadcrumbs--item {
  white-space: nowrap;
}

.breadcrumbs--item:not(:last-child) {
  margin-right: 0.5rem;
}

.breadcrumbs--divider:not(:last-child) {
  margin-right: 0.5rem;
}

[data-folder] {
  --d-flex: flex;
  display: var(--d-flex);
  align-items: center;
  cursor: pointer;
}

[data-folder-body] {
  display: none;
}

.folder--arrow {
  --icon-size: 1.5rem;
  --margin-l: auto;
  margin-left: var(--margin-l);
  color: inherit;
  transition: 0.3s;
}

.folder--arrow:after {
  content: "\e5cf";
}

.active > .folder--arrow {
  transform: rotate(-180deg);
}

.form .form--checkbox-icon {
  align-self: start;
}

.form--grid {
  --grid-gap-x: 0.5rem;
  --grid-gap-y: 0.5rem;
}

.form--text {
  --d-block: block;
  display: var(--d-block);
  position: relative;
  cursor: text;
}

.form--text input {
  width: 100%;
  padding: 0.8125rem 0.6875rem;
  background: none;
  border: transparent 1px solid;
  outline: none;
  background: #fff;
  border-radius: 0.25rem;
  transition: 0.3s;
}

.form--text input::placeholder {
  color: transparent;
}

.form--text input:not(:placeholder-shown):invalid ~ .form--text-placeholder {
  opacity: 0;
  z-index: -1;
}

.form--text input:not(:placeholder-shown):invalid ~ .form--text-error {
  opacity: 1;
  z-index: 1;
}

.form--text input:not(:placeholder-shown) ~ .form--text-placeholder {
  opacity: 0;
  z-index: -1;
}

.form--text input:focus {
  border-color: #E06733;
}

.form--text input:focus ~ .form--text-placeholder {
  opacity: 0;
  z-index: -1;
}

.form--text input:focus ~ .form--text-error {
  opacity: 0 !important;
  z-index: -1 !important;
}

.form--text-placeholder {
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
  padding: 0.8125rem 0.6875rem;
  user-select: none;
  color: #6c6c6c;
  transition: 0.3s;
  z-index: 1;
}

.form--text-placeholder span {
  color: #e75c5c;
}

.form--text-error {
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
  padding: 0.8125rem 0.6875rem;
  user-select: none;
  color: #e75c5c;
  background: #fff2f2;
  border: #fdd3d3 1px solid;
  opacity: 0;
  transition: 0.3s;
  border-radius: 0.25rem;
  z-index: -1;
}

.form--textarea {
  --d-block: block;
  display: var(--d-block);
  position: relative;
  cursor: text;
}

.form--textarea textarea {
  width: 100%;
  height: 8.5rem;
  resize: none;
  padding: 2.4375rem 0.9375rem 0.6875rem;
  background: none;
  border: transparent 1px solid;
  outline: none;
  background: #fff;
  border-radius: 0.25rem;
  transition: 0.3s;
}

.form--textarea textarea::placeholder {
  color: transparent;
}

.form--textarea textarea:not(:placeholder-shown):invalid ~ .form--textarea-placeholder {
  opacity: 0;
  z-index: -1;
}

.form--textarea textarea:not(:placeholder-shown):invalid ~ .form--textarea-error {
  opacity: 1;
  z-index: 1;
}

.form--textarea textarea:not(:placeholder-shown) ~ .form--textarea-placeholder {
  opacity: 0;
  z-index: -1;
}

.form--textarea textarea:focus {
  border-color: #E06733;
}

.form--textarea textarea:focus ~ .form--textarea-placeholder {
  opacity: 0;
  z-index: -1;
}

.form--textarea textarea:focus ~ .form--textarea-error {
  opacity: 0 !important;
  z-index: -1 !important;
}

.form--textarea textarea.limited ~ .form--textarea-counter {
  color: #e75c5c;
}

.form--textarea-placeholder {
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
  padding: 2.4375rem 0.9375rem 0.6875rem;
  user-select: none;
  color: #6c6c6c;
  transition: 0.3s;
  z-index: 1;
}

.form--textarea-placeholder span {
  color: #e75c5c;
}

.form--textarea-error {
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
  padding: 2.4375rem 0.9375rem 0.6875rem;
  user-select: none;
  color: #e75c5c;
  background: #fff2f2;
  border: #fdd3d3 1px solid;
  transition: 0.3s;
  border-radius: 0.25rem;
  z-index: -1;
}

.form--textarea-counter {
  display: flex;
  align-items: center;
  width: calc(100% - 2rem);
  position: absolute;
  left: 1rem;
  top: 0;
  padding: 0.75rem 0 0.25rem;
  font-size: 0.875rem;
  line-height: 1.14;
  color: #a3a3a3;
  border-bottom: #ebebeb 1px solid;
  transition: 0.3s;
}

.form--textarea-counter-hint {
  margin-right: auto !important;
  font-size: 0.8125rem;
  font-weight: 500;
}

.form--checkbox {
  --d-flex: flex;
  display: var(--d-flex);
  align-items: center;
  padding: 0.5rem 0;
  cursor: pointer;
}

.form--checkbox a {
  color: #636060;
}

.form--checkbox input {
  display: none;
}

.form--checkbox input:checked ~ .form--checkbox-icon {
  background: #636060;
  fill: #fff;
}

.form--checkbox-icon {
  --icon-size: 1.125rem;
  border: #636060 1px solid;
  fill: transparent;
  transition: 0.3s;
}

.modal {
  overflow: auto;
  --d-block: none;
  display: var(--d-block);
  width: 19.5rem;
  max-width: 100vw;
  max-height: 100vh;
  position: fixed;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  background: #ebebeb;
  z-index: 100;
}

.modal.active {
  --d-block: block;
}

.modal .btn--size-sm {
  position: absolute;
  top: 0.5rem;
  right: 0.5rem;
  color: #a3a3a3;
}

.modal .btn--size-sm .icon:after,
.modal .btn--size-sm .folder--arrow:after,
.modal .btn--size-sm .form--checkbox-icon:after {
  content: "\e5cd";
}

.modal--buttons {
  display: var(--d-flex);
  justify-content: center;
}

.modal--buttons .btn--size-md {
  --padding-l: 2rem;
  --padding-r: 2rem;
}

.modal-shadow {
  display: none;
  width: 100%;
  height: 100vh;
  position: fixed;
  left: 0;
  top: 0;
  background: rgba(0, 0, 0, 0.4);
  z-index: 97;
}

.pagination {
  --d-flex: flex;
  display: var(--d-flex);
  justify-content: center;
  align-items: center;
  text-align: center;
}

.pagination--first .icon:after,
.pagination--first .folder--arrow:after,
.pagination--first .form--checkbox-icon:after {
  content: "\eac3";
}

.pagination--prev .icon:after,
.pagination--prev .folder--arrow:after,
.pagination--prev .form--checkbox-icon:after {
  content: "\e5cb";
}

.pagination--next .icon:after,
.pagination--next .folder--arrow:after,
.pagination--next .form--checkbox-icon:after {
  content: "\e5cc";
}

.pagination--last .icon:after,
.pagination--last .folder--arrow:after,
.pagination--last .form--checkbox-icon:after {
  content: "\eac9";
}

.pagination--page {
  --d-block: block;
  display: var(--d-block);
  flex: 0 0 1.4375rem;
  padding: 0.5rem 0;
  text-decoration: none;
  color: inherit;
}

.pagination--page:visited {
  color: #6c6c6c;
}

.pagination--divider {
  --d-block: block;
  display: var(--d-block);
  flex: 0 0 1.4375rem;
  padding: 0.5rem 0;
}

.pagination--current {
  flex: 0 0 1.4375rem;
  padding: 0.5rem 0 0.375rem;
  font-weight: 500;
  color: #636060;
  border-bottom: #636060 2px solid;
}

.tabs {
  --d-grid: grid;
  display: var(--d-grid);
  grid-template-areas: "header" "body";
}

.tabs--header {
  --d-flex: flex;
  display: var(--d-flex);
}

.tabs--btn {
  flex-grow: 1;
  padding: 1rem;
