@media (max-width: 991px) {
  .breadcrumbs a {
    padding: 0.5rem 1rem;
    background: #fff;
    border-radius: 2.25rem;
  }

  .breadcrumbs--item {
    padding: 0.5rem 0;
  }

  .breadcrumbs--divider {
    display: none;
  }
}

@media (max-width: 767px) {
  .modal--buttons {
    flex-direction: column;
  }

  .header {
    position: fixed;
    width: 100%;
    background: #EDF2EC;
    z-index: 2;
  }

  .header-fixed {
    min-height: 6rem;
    width: 100%;
  }

  .menu {
    position: fixed;
    top: 0;
    left: 100%;
    background: #EDF2EC;
    width: 100%;
    height: 100%;
    z-index: 1;
    overflow: auto;
    transition: 0.3s;
  }

  .menu-link a {
    line-height: 1.25rem;
  }
}

@media (pointer: fine) {
  .form--text input:hover {
    background: #e2f9cc;
  }

  .form--text input:hover ~ .form--text-placeholder {
    color: #000;
  }

  .form--textarea textarea:hover {
    background: #e2f9cc;
  }

  .form--textarea textarea:hover ~ .form--textarea-placeholder {
    color: #000;
  }

  .form--checkbox:hover .form--checkbox-icon {
    background: #e2f9cc;
  }

  .form--checkbox:hover input:checked ~ .form--checkbox-icon {
    background: #8ecf98;
    border-color: #8ecf98;
  }

  .pagination--page:hover {
    color: #636060;
  }

  .tabs--btn:not(.active):hover {
    background: #d7d7d7;
  }
}